@mixin sd {
    @media (min-width:#{$screen-sd-min}) and (max-width: #{$screen-sd})
    {
        @content;
    }
}

@mixin md {
    @media (min-width: #{$screen-sd}) and (max-width: #{$screen-md})
    {
        @content;
    }
}

@mixin lg {
    @media (min-width: #{$screen-md}) and(max-width: #{$screen-lg})
    {
        @content;
    }
}

@mixin xl {
    @media (min-width: #{$screen-xl})
    {
        @content;
    }
}
 

    