.header
{
    background-image: url(../../assets/images/groupe.jpg);
    background-size: cover;
    height: 100vh;
    width: 100%;
    background-position: center center;
    position: relative;
    background-repeat: no-repeat;
    margin: 0;
    padding: 0;

    @include xl {
        background-image: url(../../assets/images/groupe2.jpg);
    }
   
    &__titre
    {
        color: white;
        background-color: $c-black1;
        /* display: block;
        margin: 0 auto; */
        text-align: center;
        width: 70%;
        padding: 0.8em 0.7em;
        position: absolute;
        top: 45%; left: 50%; /* à 50%/50% du parent référent */
        transform: translate(-50%, -50%); /* décalage de 50% de sa propre taille */
        //bottom: 10vh;
        @include md 
        {
            width: 380px;
            padding: 0.9em 0.7em;
        }
        @include lg
        {
            width: 340px;
            font-size: 2.25em;
            padding: 0.9em 0.7em;
        }
        @include xl {
            font-size: 2.5em;
            width: 20%;
        }
    }
    &__sub{
        position: absolute;
         top: 51%; left: 50%; /* à 50%/50% du parent référent */
         transform: translate(-50%, -50%); 
         font-weight: bold;
         @include xl {
             font-size: 1.2em;
             font-weight: 200;
         }
    }
    &__downbtn
    {
        content: "";
      //  margin: 0 auto;
        //text-align: center;
        position: absolute;
        bottom: 5%; left: 45%; /* à 50%/50% du parent référent */
        transform: translate(-50%, -50%); /* décalage de 50% de sa propre taille */
        background-size: contain;
        width: 10%;
        height: 60px;
        background-image: url(../../assets/icons/down-button.svg);
        background-position: center center;
        background-repeat: no-repeat;
        margin: 0;
        padding: 0;
    }
}
