.title
{
    font-family: 'Roboto Slab', serif;
    padding: 0;
    margin: 0;
    font-size: 19px;
    @include sd 
    {
        font-size: 23px;
    }
    @include md 
    {
        font-size: 27px;
    }
    @include lg 
    {
        font-size: 32px;
    }
    @include xl 
    {
        font-size: 35px;
    }
    &__section
    {
        text-transform: uppercase;
        line-height: 1.4em;
        padding-bottom: 3vh;
        
        @include lg 
        {
            padding-bottom: 0.5em;
        }
        @include xl
        {
            padding-bottom: 1em;
        }
        &::after
        {
            content: "";
            display: block;
            background-color: $c-black1;
            height: 7px;
            width: 1.8em;
            margin-top: 0.1em;
        }
    
        &--center
        {
            display: block;
            margin: 0 auto;
            text-align: center;
            &::after
            {
                margin: 0.1em auto 0 auto;
                text-align: center;
            }
        }
    }
}
