.rux
{
    @include lg 
    {
        
    grid-template-areas: 
    "l1c1 l1c2"
    "l2c1 l2c2"; 
    .l1c1{grid-area:l1c1;} 
    .l1c2{grid-area:l1c2;} 
    .l2c1{grid-area:l2c1;
    margin-top: 20px;} 
    .l2c2{grid-area:l2c2;} 
    }
    @include xl
    {
        grid-template-areas: 
        "l1c1 l1c2"
        "l2c1 l2c2"; 
        .l1c1{grid-area:l1c1;} 
        .l1c2{grid-area:l1c2;} 
        .l2c1{grid-area:l2c1;
        margin-top: 20px;} 
        .l2c2{grid-area:l2c2;} 
    
    }
    
    
}

.kikk
{
    @include lg 
    {
    grid-template-areas: 
    "l1c1 l1c1"
    "l2c1 l2c2"
    "l3c1 l3c1"; 
    .l1c1{grid-area:l1c1;} 
    .l2c1{grid-area:l2c1;} 
    .l2c2{grid-area:l2c2;} 
    .l3c1{grid-area:l3c1;} 
    }
    @include xl
    {
    
        //grid-column-gap: 4vw;
        grid-template-areas: 
        "l1c1 l1c1"
        "l2c1 l2c2"
        "l3c1 l3c1"; 
        .l1c1{grid-area:l1c1;} 
    .l2c1{grid-area:l2c1;} 
    .l2c2{grid-area:l2c2;} 
    .l3c1{grid-area:l3c1;} 
    }
    
    
}

.tacheprb
{
    @include lg 
    {
        grid-row-gap: 0vh;
        grid-template-areas: 
        "l1c1 l1c2"
        "l2c1 l2c2"
        "l3c1 l2c2";
        .l1c1{grid-area:l1c1;
            /*margin-bottom: 2.5vh;*/
        } 
    .l1c2{grid-area:l1c2;
        
    } 
    .l2c1{grid-area:l2c1;
        margin-bottom: 0;
        padding-bottom: 0;
    } 
    .l2c2{grid-area:l2c2;} 
    .l3c1{
            grid-area:l3c1;
            margin-top: 0;
            padding-top: 0;

            &::before
            {
                content: ______________;
                display: block;
                height: 10px;
                width: 100px;
            }
        } 
    }

    @include xl
    {
        grid-row-gap: 0vh;
        grid-template-areas: 
        "l1c1 l2c2"
        "l2c1 l2c2"
        "l3c1 l2c2";
        .l1c1{grid-area:l1c1;
            /*margin-bottom: 2.5vh;*/
        } 
    .l1c2{grid-area:l1c2;
        
    } 
    .l2c1{grid-area:l2c1;
        margin-bottom: 0;
        padding-bottom: 0;
    } 
    .l2c2{grid-area:l2c2;} 
    .l3c1{
            grid-area:l3c1;
            margin-top: 0;
            padding-top: 0;

            &::before
            {
                content: ______________;
                display: block;
                height: 10px;
                width: 100px;
            }
        } 
    }
  
}


.idprb
{
    .l3c1{grid-area:l3c1;
        & figcaption
        {
            margin: -3.5vh auto 0 auto;
        }
    }
    @include lg 
    {
        grid-template-areas: 
        "l1c1 l1c2"
        "l2c1 l2c2"
        "l3c1 l3c1";
        .l1c1{grid-area:l1c1;} 
    .l1c2{grid-area:l1c2;} 
    .l2c1{grid-area:l2c1;} 
    .l2c2{grid-area:l2c2;} 
    .l3c1{grid-area:l3c1;} 
    }
    
    @include xl
    {
        grid-template-areas: 
        "l1c1 l1c2"
        "l2c1 l2c2"
        "l3c1 l3c1";
        .l1c1{grid-area:l1c1;} 
    .l1c2{grid-area:l1c2;} 
    .l2c1{grid-area:l2c1;} 
    .l2c2{grid-area:l2c2;} 
    .l3c1{grid-area:l3c1;} 
    }
}


.avosmarque{
   @include lg 
   {
       grid-template-columns: 1.5fr 2fr;
       grid-column-gap: 8vw;
       grid-template-areas: 
       "l1c1 l1c1"
       "l2c1 l2c2"
       "l3c1 l2c2"; 
       .l1c1{grid-area:l1c1;
           text-align: center;} 
       .l2c1{grid-area:l2c1;} 
       .l2c2{grid-area:l2c2;} 
          
   }
   @include xl
   {
      

       grid-template-areas: 
       "l1c1 l1c1"
       "l2c1 l2c2"; 
       .l1c1{grid-area:l1c1;
           text-align: center;} 
       .l2c1{grid-area:l2c1;} 
       .l2c2{grid-area:l2c2;} 
         
   }
}


.solution
{
    background-color: $c-gris-1;
    &__titre
    {
        &::after
        {
            margin: 0 auto;
            text-align: center;
        }
    }
    @include lg 
    {
        grid-template-areas: 
        "l1c1 l1c1"
        "l2c1 l2c1"
        "l3c1 l3c2"
        "l4c1 l4c1";
    .l1c1,.l2c1
    {
        
    }
    .l1c1{grid-area:l1c1;
        text-align: center;} 
    .l1c2{grid-area:l1c2;} 
    .l2c1{grid-area:l2c1;} 
    .l3c1{grid-area:l3c1;} 
    .l3c2{grid-area:l3c2;} 
    .l4c1{grid-area:l4c1;}
    }
    @include xl
    {
        grid-column-gap: 5vw;
        grid-template-areas: 
        "l1c1 l1c1"
        "l2c1 l2c1"
        "l3c1 l3c2"
        "l4c1 l4c1";
    .l1c1,.l2c1
    {
        
    }
    .l1c1{grid-area:l1c1;
        text-align: center;} 
    .l1c2{grid-area:l1c2;} 
    .l2c1{grid-area:l2c1;} 
    .l3c1{grid-area:l3c1;} 
    .l3c2{grid-area:l3c2;} 
    .l4c1{grid-area:l4c1;}
    }    
}